.nav-social {
    /* height: 36.7%; */
    flex-grow: 1.5;
    width: 100%;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: flex-start;
    /* margin-left: 10%; */
    border-top: 0.45px solid #202020;
    padding-top: 10%;
    padding-bottom: 0%;
    /* background-color: red; */
    /* overflow: hidden; */
}
.nav-social .links {
    /* height: 80%; */
    background-color: #121212;
    width: 90%;
    border-radius: 14.29px;
    font-weight: 500;
    font-size: 0.78em;
    line-height: 1.1875em;
    color: #ffffff;
    padding-left: 10%;
    padding-top: 5%;
    padding-bottom: 5%;
    margin-top: 5%;
}
.nav-social .links ul {
    width: 90%;
    list-style-type: none;
    padding: 0;
}

.nav-social .links ul li {
    /* width: 70%; */
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 12%;
}
.nav-social .links ul li p {
    margin: 0;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
.nav-out {
    /* height: 15.3%; */
    flex-grow: 0.5;
    width: 100%;
    display: flex;
    /* flex-direction: column; */
    justify-content: center;
    align-items: center;
    border-top: 0.45px solid #202020;
    padding-top: 10%;
    padding-bottom: 5%;
    font-weight: 600;
    font-size: 1em;
    line-height: 1.6875em;
    color: #ff295d;
    cursor: pointer;
    /* background-color: royalblue; */
    /* overflow: hidden; */
}
